#content {
    position: absolute;
    top: 50%;
    left: 50%;
    text-align: center;
    transform: translate(-50%, -50%);
}

#filter {
    position: fixed;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
}

#text {
    width: 800px;
    min-width : 50vw;
    max-width : 90vw;
    max-height : 50vw;
    text-align: center;
    -ms-overflow-style: none;
}

#text::-webkit-scrollbar {
    display: none;
}

#about {
    position: fixed;
    bottom: 24px;
    right: 24px;
    display: flex;
    background-color: white;
}